<template>
  <div id="container">
    <youtube :video-id="videoId" player-width="836" player-height="470" />

    <div id="editor">
      <div id="paper">
        <Mirage :color="color" :roundel="roundel" @initColor="initColor" />
      </div>

      <div>
        <v-color-picker
          v-model="color"
          dot-size="25"
          hide-inputs
          :swatches="swatches"
          show-swatches
        ></v-color-picker>

        <roundel-picker v-model="roundel" />

        <v-select
          :items="skins"
          label="Skin"
          solo
        ></v-select>

        <v-btn block v-on:click="printOut('paper')">Print</v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import Mirage from "./Mirage.vue";
import printJS from "print-js";
import RoundelPicker from "./RoundelPicker.vue";
import { getIdFromURL } from "vue-youtube-embed";

export default {
  components: { Mirage, RoundelPicker },
  data() {
    return {
      color: "#ffffff",
      roundel: "swiss",
      skin: 1,
      videoId: getIdFromURL("https://youtu.be/2ZnIgt4x4Yg"),
      swatches: [
        ['#FF0000', '#AA0000', '#550000'],
        ['#FFFF00', '#AAAA00', '#555500'],
        ['#00FF00', '#00AA00', '#005500'],
        ['#00FFFF', '#00AAAA', '#005555'],
        ['#0000FF', '#0000AA', '#000055'],
      ],
      skins: [1, 2],
    };
  },
  methods: {
    initColor(color) {
      console.log(`initColor(${color})`);
      this.color = color;
    },
    printOut(id) {
      printJS({
        printable: id,
        header: "Mirage",
        type: "html",
        style:
          "#paper { width:15cm; height:15cm; margin-left:auto; margin-right: 0;}",
        scanStyles: false,
      });
    },
  },
};
</script>

<style scoped>
#container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
#editor {
  display: flex;
}
#paper {
  width: 536px;
  height: 536px;
  border: 5px;
}
</style>
