<template>
  <div class="roundels">
    <div v-for="roundel in roundels" :key="roundel" style="width: 64px">
      <img
        class="roundel"
        :src="assetPath(roundel)"
        :width="roundelRatio(roundel) * 32"
        v-on:click="setRoundel(roundel)"
      />
    </div>
  </div>
</template>

<script>
export default {
    props: ['value'],

  data() {
    return {
      roundel: this.value,
      roundels: [
        "france",
        "saudi",
        "canada",
        "swiss",
        "us",
        "greece",
        "german",
        "soviet",
        "korea",
        "japan",
        "czech",
        "india",
        "italy",
        "poland",
        "romania",
        "singapore",
        "spain",
        "sweden",
        "uk",
        "ukraine",
      ],
      roundelsRatios: { us: 204.0 / 108, korea: 1160 / 481.4 },
    };
  },
  methods: {
    assetPath(roundel) {
      var images = require.context("@/assets/roundels", false, /\.svg$/);
      return images("./" + roundel + ".svg");
    },
    setRoundel(roundel) {
      this.roundel = roundel;
      this.$emit('input', roundel)
    },
    roundelRatio(id) {
      var ratio = this.roundelsRatios[id];
      if (ratio === undefined) {
        return 1.0;
      }
      return ratio;
    },
  },
};
</script>

<style scoped>
.roundel {
  margin: 5px auto;
  display: flex;
}
.roundels {
  display: flex;
  flex-flow: wrap;
  margin: 5px;
  overflow-y: scroll;
  width: 294px;
  height: 130px;
}
</style>